import React from 'react'
import {
  CContainer,
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CCollapse,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CFade,
  CForm,
  CFormGroup,
  CFormText,
  CValidFeedback,
  CInvalidFeedback,
  CTextarea,
  CInput,
  CInputFile,
  CInputCheckbox,
  CInputRadio,
  CInputGroup,
  CInputGroupAppend,
  CInputGroupPrepend,
  CDropdown,
  CInputGroupText,
  CLabel,
  CSelect,
  CRow,
  CSwitch
} from '@coreui/react'



const SuratTugas = () => {
  const [collapsed, setCollapsed] = React.useState(true)
  const [showElements, setShowElements] = React.useState(true)

  return (
    <>
      <CRow>
        <CCol xs="12">
          <CFade timeout={300} in={showElements} unmountOnExit={true}>
            <CCard>
              <CCardHeader>
               <h4>Form Pembuatan Surat Tugas</h4> 
                {/* <div className="card-header-actions">
                  <CButton color="link" className="card-header-action btn-setting">
                    <CIcon name="cil-settings" />
                  </CButton>
                  <CButton 
                    color="link" 
                    className="card-header-action btn-minimize" 
                    onClick={() => setCollapsed(!collapsed)}
                  >
                    <CIcon name={ collapsed ? "cil-arrow-top" : "cil-arrow-bottom"} />
                  </CButton>
                  <CButton 
                    color="link" 
                    className="card-header-action btn-close" 
                    onClick={() => setShowElements(false)}
                  >
                    <CIcon name="cil-x" />
                  </CButton>
                </div> */}
              </CCardHeader>
              <CCollapse show={collapsed} timeout={1000}>
                <CCardBody>
                
                
              <CRow>
                <CCol xs="4">
                  
                  <CFormGroup >
                  <CLabel htmlFor="number">Nomor Surat Tugas</CLabel>
                    <CInput id="number" placeholder="Masukkan Nomor Surat Tugas" />
                  </CFormGroup>
                </CCol>
                <CCol>
                <CFormGroup row>
                  <CCol md="9">
                    <CLabel htmlFor="date-input">Tanggal Surat Tugas</CLabel>
                  </CCol>
                  <CCol xs="6" >
                    <CInput type="date" id="date-input" name="date-input" placeholder="date" />
                  </CCol>
                </CFormGroup>
                  
                </CCol>
            
              </CRow>
              

              <CRow>
                <CCol xs="4">
                  <CFormGroup>
                    <CLabel htmlFor="ccmonth">Pilih Kampus</CLabel>
                    <CSelect custom name="ccmonth" id="ccmonth">
                    <option value="1">A UNJ</option>
                      <option value="2">B UNJ</option>
                    </CSelect>
                  </CFormGroup>
                </CCol>
                
                <CCol xs="4">
                  <CFormGroup>
                    <CLabel htmlFor="ccyear">Pilih Gedung</CLabel>
                    <CSelect custom name="ccyear" id="ccyear">
                    <option>Gedung Ra. Kartini</option>
                      <option>Gedung Dewi Sartika</option>
                      <option>Gedung FIP</option>
                      <option>Gedung MIPA</option>
                    </CSelect>
                  </CFormGroup>
                  
                </CCol>
            
              </CRow>
              <hr></hr>
              <CFormGroup row>
               
                  <CCol md="3">
                    <CLabel htmlFor="select">Nama Petugas</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CSelect custom name="select" id="select">
                      <option value="0">Pilihan Petugas</option>
                      <option value="1">Dani</option>
                      <option value="2">Didi</option>
                      <option value="3">Joni</option>
                      
                    </CSelect>
                    {/* <div className="form-actions" style={{padding: '5px 5px'}}>
                      <CButton type="submit" href="/login" color="primary">Simpan</CButton>
                    </div> */}
                  </CCol>
                </CFormGroup>
              <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="date-input">Tanggal Pemeriksaan</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CInput type="date" id="date-input" name="date-input" placeholder="date" />
                  </CCol>
                </CFormGroup>
                  <CForm className="form-horizontal">
                  <CRow>
                <CCol xs="12">
                <CFormGroup row>
                  <CCol lg="3">
                    <CLabel htmlFor="select">Nomor Urut Aset</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">01</option>
                      <option value="2">02</option>
                      <option value="3">03</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Jenis Aset</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Option #1</option>
                      <option value="2">Option #2</option>
                      <option value="3">Option #3</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Tipe Aset</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Option #1</option>
                      <option value="2">Option #2</option>
                      <option value="3">Option #3</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Kegunaan Surat Tugas</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Pemeriksaan Berkala</option>
                      <option value="2">Pengaduan</option>
                   
                    </CSelect>
                  </CCol>
                </CFormGroup>
            
                {/* <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Tipe Aset</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CSelect custom name="select" id="select">
                      <option value="0">Please select</option>
                      <option value="1">Option #1</option>
                      <option value="2">Option #2</option>
                      <option value="3">Option #3</option>
                    </CSelect>
                  </CCol>
                </CFormGroup> */}


                </CCol>
              </CRow>
                    
                  <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="textarea-input">Deskripsi Surat Tugas</CLabel>
                  </CCol>
                  <CCol xs="5" >
                    <CTextarea 
                      name="textarea-input" 
                      id="textarea-input" 
                      rows="5"
                      placeholder="Deskripsikan Keperluan Surat Tugas Pemeriksaan..." 
                    />
                  </CCol>
                </CFormGroup>
                
                <CFormGroup row></CFormGroup>
                    {/* </CFormGroup> */}
                    <div className="form-actions">
                      <CButton type="submit" color="primary">Simpan</CButton>
                      <CButton color="secondary">Cetak</CButton>
                    </div>
                  </CForm>
                </CCardBody>
              </CCollapse>
            </CCard>
          </CFade>
        </CCol>
      </CRow>
    </>
  )
}

export default SuratTugas